import Translate from './Translate'
import { LanguageContext } from '../landing'
import React, { useState, useContext, useEffect, useRef } from 'react'
import { useNavigate, Link } from 'react-router-dom'

function Nav({ lenis, setLang, setValue }) {
	const navigate = useNavigate()
	// Set the main header and mobile nav refs to update their classes dynamically
	const mainHeaderRef = useRef(null)
	const mobileNavRef = useRef(null)

	// Get the language and the Curbcut City svg from the context
	const contextValue = useContext(LanguageContext)
	const lang = contextValue.lang
	const configState = contextValue.configState
	if (!configState) {
		return null
	}
	const c_city_svg = configState.c_city_svg

	// Set the state for the main header and mobile nav
	const [mainHeaderOpened, setMainHeaderOpened] = useState(false)
	const [mobileNavVisible, setMobileNavVisible] = useState(false)
	const [lenisAnimation, setLenisAnimation] = useState(true)

	// // Toggle the main header and mobile nav
	const toggleMenu = () => {
		setMainHeaderOpened(!mainHeaderOpened)
		setMobileNavVisible(!mobileNavVisible)

		if (!mobileNavVisible) {
			mainHeaderRef.current.classList.add('header--opened')
			mobileNavRef.current.classList.add('mobile-nav--visible')
			setLenisAnimation(false)
		} else {
			mainHeaderRef.current.classList.remove('header--opened')
			mobileNavRef.current.classList.remove('mobile-nav--visible')
			setLenisAnimation(true)
		}
	}

	// Hide the mobile nav when a link is clicked
	useEffect(() => {
		if (lenisAnimation) {
			lenis.start()
		} else {
			lenis.stop()
		}
	}, [lenisAnimation, lenis])

	// When the user scrolls down, the header will hide. When the user scrolls up, the header will show.
	useEffect(() => {
		// If the mainHeader (mobile) is opened, do not bother with the scroll
		if (mainHeaderOpened) return

		// Hide the header on scroll function
		const handleScroll = (event) => {
			const topPageScrollPosition = 3
			if (
				event.direction === 1 &&
				event.animatedScroll > topPageScrollPosition
			) {
				mainHeaderRef.current.classList.add('header--hidden')
			} else {
				mainHeaderRef.current.classList.remove('header--hidden')
			}

			if (event.animatedScroll < topPageScrollPosition) {
				mainHeaderRef.current.classList.remove('header--background')
			} else mainHeaderRef.current.classList.add('header--background')
		}

		// Add the scroll event listener
		lenis.on('scroll', handleScroll)
		return () => {
			lenis.off('scroll', handleScroll)
		}
	}, [lenis, mainHeaderOpened])

	return (
		<React.Fragment>
			<header ref={mainHeaderRef} id='main-header'>
				<img
					onClick={() => navigate('/')}
					style={{ cursor: 'pointer' }}
					className='curbcut-logo'
					src='data:image/jpg;base64,'
					alt='Curbcut'
					width='124'
					height='25'
				/>
				<nav className='main-nav'>
					<ul>
						<li>
							<div>
								<Link to='/'>
									<img
										src={c_city_svg}
										alt=''
										width='115'
										height='24'
									/>
								</Link>
							</div>
						</li>
						<li>
							<Link
								to='/about'
								className='cta-text cta-text--underline'
							>
								<Translate str='About Curbcut' />
							</Link>
						</li>
						<li>
							<Link
								to='/team'
								className='cta-text cta-text--underline'
							>
								<Translate str='The Team' />
							</Link>
						</li>
						<li>
							<a
								href='mailto:contact@curbcut.ca'
								className='cta-text cta-text--underline'
							>
								<Translate str='Contact Us' />
							</a>
						</li>
						<li className='language-switcher'>
							<span
								onClick={() => {
									const newLang = lang === 'fr' ? 'en' : 'fr'
									setLang(newLang)
									setValue({
										event: 'lang_change',
										lang: newLang,
									})
								}}
								className='cta-text cta-text--underline'
							>
								<Translate str='FR' />
							</span>
						</li>
						<li
							className='menu-btn'
							id='main-menu-btn'
							onClick={toggleMenu}
						></li>
					</ul>
				</nav>
			</header>
			<nav id='mobile-nav' ref={mobileNavRef}>
				<div className='mobile-nav-group'>
					<div className='h2'>
						<Translate str='About' />
					</div>
					<ul>
						<li>
							<Link to='/about'>
								<Translate str='About Curbcut' />
							</Link>
						</li>
						<li>
							<Link to='/team'>
								<Translate str='The Team' />
							</Link>
						</li>
						<li>
							<Link to='mailto:contact@curbcut.ca'>
								<Translate str='Contact Us' />
							</Link>
						</li>
					</ul>
				</div>
				{[...Array(10)].map((e, i) => (
					<span key={i} className='dot'></span>
				))}
			</nav>
		</React.Fragment>
	)
}

export default Nav
