  .landing footer .footer__content > div:not(.footer__content-copyright) {
    display: flex;
    flex-direction: column;
    gap: 44px;
  }
}
.landing footer .footer__content > div:not(.footer__content-copyright) .grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(10px, 1fr));
  column-gap: 50px;
  row-gap: 30px;
}
@media (max-width: 1170px) {
  .landing footer .footer__content > div:not(.footer__content-copyright) .grid {
    display: flex;
    flex-direction: column;
  }
}
.landing footer .footer__content > div:not(.footer__content-copyright) .grid .column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}
@media (max-width: 1170px) {
  .landing footer .footer__content > div:not(.footer__content-copyright) .grid .column:not(:last-of-type) {
    padding-bottom: 50px;
    border-bottom: 1px solid #000000;
  }
}
.landing footer .footer__content > div:not(.footer__content-copyright) .grid .column__title {
  display: inline-flex;
  align-items: center;
  height: 22px;
  margin-bottom: 2px;
  padding: 0 7px;
  font-size: 1.4rem;
  line-height: 1.7rem;
  white-space: nowrap;
  border: 1px solid #101010;
  border-radius: 6.4px;
}
@media (max-width: 1170px) {
  .landing footer .footer__content > div:not(.footer__content-copyright) .grid .column__title {
    padding: 0;
    font-size: 2.5rem;
    line-height: 3.1rem;
    height: auto;
    border: none;
  }
}
.landing footer .footer__content > div:not(.footer__content-copyright) .grid .column img:first-of-type {
  margin-top: 15px;
}
.landing footer .footer__content > div:not(.footer__content-copyright) .grid .column img:not(:last-child) {
  margin-bottom: 24px;
}
.landing footer .footer__content-first-half {
  padding-bottom: 60px;
  border-bottom: 1px solid #000000;
}
@media (max-width: 1170px) {
  .landing footer .footer__content-first-half {
    padding-bottom: 10px;
    border-bottom: none;
  }
}
.landing footer .footer__content-second-half {
  padding-top: 42px;
}
@media (max-width: 1170px) {
  .landing footer .footer__content-second-half .grid > .column--cities {
    display: block !important;
    max-height: 170px;
  }
  .landing footer .footer__content-second-half .grid > .column--cities .column__title {
    display: block !important;
  }
  .landing footer .footer__content-second-half .grid > .column--cities img {
    margin-bottom: 0 !important;
    display: inline-flex;
    align-items: center;
  }
  .landing footer .footer__content-second-half .grid > .column--cities img:not(:last-child) {
    margin-right: 40px;
  }
}
.landing footer .footer__content-copyright {
  display: block;
  text-align: center;
  margin-top: 42px;
}
@media (max-width: 1170px) {
  .landing footer .footer__content .hidden-mobile {
    display: none !important;
  }
}
.landing footer form {
  width: 100%;
}
.landing footer input {
  height: 46px;
  border: 1px solid #000000;
  border-radius: 6.4px;
  padding: 0 16px;
  width: min(208px, 100%);
}
@media (max-width: 1170px) {
  .landing footer input {
    width: 100%;
  }
}
.landing footer input, .landing footer input::placeholder {
  font-family: "_AcidGrotesk", sans-serif;
  font-size: 1.4rem;
}
.landing footer .submit-btn {
  margin-top: 11px;
  color: #ffffff;
  background-color: #000000;
  height: 46px;
  min-width: 124px;
  padding: 0 16px;
  border-radius: 8px;
  font-size: 1.6rem;
  line-height: 2.2rem;
  cursor: pointer;
}
@media (max-width: 1170px) {
  .landing footer .submit-btn {
    min-width: 188px;
  }
}

.landing .section-team {
  margin-top: 84px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .landing .section-team {
    margin-top: 78px;
  }
}
.landing .section-team > div:first-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.landing .section-team__cards-wrapper {
  margin-top: 20px;
  display: grid;
  grid-template-columns: repeat(4, minmax(10px, 1fr));
  gap: 18px;
}
@media (max-width: 767px) {
  .landing .section-team__cards-wrapper {
    margin-top: 11px;
    grid-template-columns: repeat(2, minmax(10px, 1fr));
    gap: 13px;
  }
}
.landing .section-team__cards-wrapper .card {
  position: relative;
  overflow: hidden;
}
.landing .section-team__cards-wrapper .card__img {
  width: 100%;
  height: 265px;
  overflow: hidden;
  border-top-left-radius: 6.4px;
  border-top-right-radius: 6.4px;
  transition: border-radius 0.25s cubic-bezier(0.65, 0, 0.35, 1);
}
@media (max-width: 1170px) {
  .landing .section-team__cards-wrapper .card__img {
    height: 168px;
  }
}
.landing .section-team__cards-wrapper .card__img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.landing .section-team__cards-wrapper .card__title {
  padding: 14px;
  min-height: 67px;
  max-height: auto;
  align-items: center;
}
@media (max-width: 767px) {
  .landing .section-team__cards-wrapper .card__title {
    height: 78px;
    padding: 9px 11px;
  }
}
.landing .section-team__cards-wrapper .card__title span {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  overflow: hidden;
}
@media (max-width: 767px) {
  .landing .section-team__cards-wrapper .card__title span {
    -webkit-line-clamp: 3;
    line-clamp: 3;
  }
}
.landing .section-team__cards-wrapper .card:hover .card__img {
  border-top-right-radius: 74px;
}
.landing .contributors-container {
  text-align: center;
}
.landing .contributor-style {
  display: inline;
  margin-right: 10px;
  margin-left: 10px;
}
.landing .contributor-dot {
  display: inline;
  width: 10px;
  height: 10px;
  user-select: none;
  padding: 0 5px;
}
@media (max-width: 767px) {
  .landing .contributor-dot {
    width: 5px;
    height: 5px;
  }
}
.landing .contributor-dot:nth-child(11n+1) {
  color: #a3b0d1;
}
.landing .contributor-dot:nth-child(11n+2) {
  color: #73ad80;
}
.landing .contributor-dot:nth-child(11n+3) {
  color: #e08565;
}
.landing .contributor-dot:nth-child(11n+4) {
  color: #cd718c;
}
.landing .contributor-dot:nth-child(11n+5) {
  color: #c9c3fa;
}
.landing .contributor-dot:nth-child(11n+6) {
  color: #f5d574;
}
.landing .contributor-dot:nth-child(11n+7) {
  color: #adb033;
}
.landing .contributor-dot:nth-child(11n+8) {
  color: #9e9090;
}
.landing .contributor-dot:nth-child(11n+9) {
  color: #beaf62;
}
.landing .contributor-dot:nth-child(11n+10) {
  color: #dca656;
}
.landing .contributor-dot:nth-child(11n+11) {
  color: #7baaaa;
}

.title-text-box {
  border-radius: 15px;
  border: 1px solid #000000;
  position: relative;
  transition: width 0.3s ease;
  padding: 15px;
  max-height: 100%;
  box-sizing: border-box;
}
.title-text-box__top {
  display: flex;
  align-items: center;
  color: white;
  padding-bottom: 10px;
}
.title-text-box__top h3 {
  margin-left: 10px;
}
.title-text-box__close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
  cursor: pointer;
}
.title-text-box .content {
  overflow-y: auto;
  overflow-x: "hidden";
  padding: 1px 0px 1px 0px;
}
.title-text-box .button-container {
  text-align: right;
}
.title-text-box .button-container button {
  text-decoration: underline;
  cursor: pointer;
  color: white;
  padding-top: 10px;
